/* --------chanceled---------------------- */


#progressbar_c {
  margin-bottom: 30px;
  overflow: hidden;
  color: #455A64;
  padding-left: 0px;
  margin-top: 30px
}

#progressbar_c li {
  list-style-type: none;
  font-size: 13px;
  width: 33.33%;
  float: left;
  position: relative;
  font-weight: 400;
  color: #455A64 !important;
  
}

#progressbar_c #step1:before {
  content: "1";
  color: #000000;
  width: 29px;
  margin-left: 15px !important;
  padding-left: 11px !important;
}


#progressbar_c #step2:before {
  content: "2";
  color: #000000;
  width: 29px;

}

#progressbar_c #step3:before {
  content: "3";
  color: #000000;
  width: 29px;
  margin-right: 15px !important;
  padding-right: 11px !important;
}

#progressbar_c li:before {
  line-height: 29px;
  display: block;
  font-size: 12px;
  background: #455A64 ;
  border-radius: 50%;
  margin: auto;
}

#progressbar_c li:after {
  content: '';
  width: 121%;
  height: 2px;
  background: #455A64;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 15px;
  z-index: -1;
} 

#progressbar_c li:before {
  line-height: 29px;
  display: block;
  font-size: 12px;
  background: #ff0000 ;
  border-radius: 50%;
  margin: auto;
}

#progressbar_c li:after {
  content: '';
  width: 121%;
  height: 2px;
  background: #455A64;
  position: absolute;
  left: 0%;
  right: 0%;
  top: 15px;
  z-index: -1;
} 

#progressbar_c li:nth-child(2):after {
  left: 50%;
  background: #7efd2f; 
  
}

#progressbar_c li:nth-child(1):after {
  left: 25%;
  width: 121%;
  background: #7efd2f; 
}
#progressbar_c li:nth-child(3):after {
  left: 25% !important;
  width: 50% !important;
  background: #ff0000; 
}

#progressbar_c li.active:before,
#progressbar_c li.active:after {
  background: #7efd2f; 
}




